.gig-container {
    position: relative;
    max-width: 600px;
    margin: 20px auto;
    padding: 20px;
    border: 1px solid #3498db;
    border-radius: 8px;
    background-color: #ecf0f1;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .gig-container h2,
  .gig-container h4,
  .gig-container button {
    margin-bottom: 10px;
  }
  
  .gig-container button {
    padding: 5px 10px;
    cursor: pointer;
    background-color: #2e95cc;
    color: #fff;
    border: none;
    border-radius: 4px;
    font-size: 14px;
  }
  
  .gig-container form {
    margin-top: 10px;
  }
  
  .gig-container input {
    width: 100%;
    padding: 8px;
    margin-bottom: 10px;
  }
  
  .gig-container h4 {
    font-size: 18px;
    margin-bottom: 10px;
  }
  
  .gig-container h4 > button {
    display: block;
    margin-top: 5px;
  }
  
  .gig-container button:last-child {
    margin-left: 10px;
  }
  
  .Application {
    display: flex;
    flex-wrap: wrap;
  }
  
  .Application button {
    margin-right: 10px;
    margin-bottom: 10px;
  }

  .overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
  }
  
  .popout {
    background-color: #fff;
    padding: 20px;
    border: 1px solid #ccc;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  }